

































import Vue from 'vue'
import { Datetime } from 'vue-datetime'
import { Button } from '@/components/Button'
import 'vue-datetime/dist/vue-datetime.min.css'
import { mapGetters, mapMutations, mapState, mapActions } from 'vuex'

export default Vue.extend({
  name: 'Edition',
  components: {
    Datetime,
    Button
  },
  props: {
    taskId: {
      type: String,
      default: undefined
    }
  },
  computed: {
    ...mapState({
      task(state) {
        if (this.taskId) {
          return this.getTaskById(this.taskId)
        }

        return {
          priority: 'low',
          date: new Date().toISOString()
        }
      }
    }),
    ...mapGetters(['getTaskById'])
  },
  methods: {
    handleSave(e: Event) {
      e.preventDefault()
      if (this.task.title && this.task.date) {
        if (this.taskId) {
          this.editTask({ id: this.taskId, ...this.task })
        } else {
          ;(async () => {
            const taskId = await this.addTask(this.task)
          })()
        }
        this.$router.push({ name: 'home' })
      } else {
        alert('Fields title and date are required')
      }
    },
    ...mapMutations(['deleteTask', 'editTask']),
    ...mapActions(['addTask'])
  }
})
